import { REGISTER_SUCCESS, REGISTER_FAIL, USER_LOADED,AUTH_ERROR, ADMINLOGIN_SUCCESS,LOGIN_FAIL, LOGIN_SUCCESS, LOGOUT, DELETE_ACCOUNT, ADMINREGISTER_SUCCESS, GET_USERS, USER_ERROR } from '../actions/types';

const initialState = {
    token: localStorage.getItem('token'),
    isAuthenticated: null,
    isAdmin: false,
    role: "user",
    loading: true,
    user: null,
    users: [],
    error: {}
}

export default function(state = initialState, action ){
    
    const { type, payload} = action;
    switch (type){
        case USER_LOADED:
            return{
                ...state,
                isAuthenticated: true,
                loading: false,
                user: payload,
                isAdmin: payload.isAdmin,
                role: payload.role
            }
        case REGISTER_SUCCESS:
        case LOGIN_SUCCESS:
            localStorage.setItem('token', payload.token);
            return{
                ...state,
                ...payload,
                isAuthenticated: true,
                loading: false
            }
        case ADMINREGISTER_SUCCESS:
        case ADMINLOGIN_SUCCESS:
            localStorage.setItem('token', payload.token);
            return{
                ...state,
                ...payload,
                isAuthenticated: true,
                loading: false
            }
        case REGISTER_FAIL:
        case AUTH_ERROR:
        case LOGIN_FAIL:
        case LOGOUT:
        case DELETE_ACCOUNT:
            localStorage.removeItem('token');
            return{
                ...state,
                token: null,
                isAuthenticated: false,
                loading: false
            }
        case GET_USERS:
            return {
            ...state,
            users: payload
            }
        case USER_ERROR:
            return{
                ...state,
                error: payload
            }
        default: 
            return state;
    }

}